@import url('https://fonts.googleapis.com/css?family=Work+Sans:400,800');

* { box-sizing: border-box; }

*, html, body { font-family: 'Work Sans', 'Helvetica', sans-serif; }

html, body { margin: 0; padding: 0; }

.container {
  max-width: 100%;
  padding: 0;
  margin: 0;
}

.shell {
  max-width: 720px;
}

a {
  text-decoration: none;
  font-weight:bold;
  letter-spacing: 0.45px;
}

a:hover {
  text-decoration: none;
}

/* Typography */
.h1 {
  font-size: 36px;
  margin-bottom: 25px;
  font-weight: bold;
  letter-spacing: 0.7px;
}

.h2 {
  font-size: 30px;
  margin: 30px 0;
}

.h3 {
  font-size: 24px;
  line-height: 1.25;
  margin: 25px 0;
  text-transform: uppercase;
}

.h4 {
  font-weight: bold;
  letter-spacing: 0.45px;
  font-size: 22px;
  line-height: 1.5;
}

.p {
  font-size: 16px;
  line-height: 1.5;
}


/* Art */

.logo {
  width: 260px;
  height: auto;
  fill: #fff;
  margin-bottom: 25px;
  filter: invert(1);
}

header {
  padding: 100px 20px;
  background: #230050;
}

header .h1 {
  margin-bottom: 15px;
}

header .h3 {
  margin-top: 15px;
}

header .button {
  padding: 10px 20px;
  display: inline-block;
  border-radius: 40px;
  background: #8CFADC;
  color: #257C64;
  transition: 0.1s linear;
}

header .button:hover {
  background: #fff;
  color: #000;

}

header .button:focus {
  background: #8CFADC;
  color: #257C64;
}

.header-info {
  color: #fff;
  max-width: 960px;
  text-align: center;
}

.mission {
  padding: 100px 40px;
  background: #d3cce3; /* fallback for old browsers */
  background: -webkit-linear-gradient(to bottom, #d3cce3, #e9e4f0); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to bottom, #d3cce3, #e9e4f0); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.mission h3 {
  font-weight: bold;
  letter-spacing: 0.45px;
}

.mission-info {
  max-width: 580px;
  margin: 0 auto;
}

.projects {
  max-width: 640px;
  margin: 0 auto;
  padding: 100px 0;
}

.work {
  margin: 0 auto;
  margin-top: 40px;
  text-align: center;
}

.workbutton {
  padding: 10px 20px;
  color: #230050;
  transition: 0.1s linear all;
  border-radius: 8px;
  font-weight: bold;
  letter-spacing: 0.45px;
  border: 2px solid #230050;
  display: inline-block;
}
.workbutton:hover {
  border: 2px solid transparent;
  background: #230050;
  color: #fff;
}

.workname {
  margin-bottom: 10px;
}

.work:last-child{
  padding-bottom: 0;
}

.work .p {
  font-weight:bold;
}

.work img {
  width: 100%;
  border-radius: 8px;
  margin-bottom: 10px;
}

.services {
  display: flex;
  flex-direction: row;
  align-items:flex-start;
  max-width: 520px;
  margin: 0 auto;
  color: #fff;
  margin-bottom: 40px;
}

.services img {
  box-shadow: 0 2px 10px #270C73;
  border-radius: 8px;

}

.servicesName {
  font-weight: bold;
  letter-spacing: 0.45px;
}

.servicesDesc {
  font-size: 14px;
}

.servicesSubline {
  margin-left: 20px;
}

.servicesDesc {
  align-items:flex-start;
}

.service {
    padding: 100px 0;
    margin: 0 auto;
    background: linear-gradient(180deg, #270C73 0%, #423D80 124.33%), #0C396E;
}

.service h1 {
  color: #fff;
  margin-bottom: 50px;
}

.service-info {
  align-items:flex-start;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  max-width: 720px;
}

.service .h1 {

}

.workinfo {
  padding: 20px 0;
}

footer {
  background: #0E0830;
  display: flex;
  justify-content: center;
  padding: 50px 20px 20px 0;
}

footer .a {
  color: #fff;
}

footer p, footer h1 {
  color: #fff;
}

footer .button {
  max-width: 320px;
  width: auto;
  padding: 10px 20px;
  border-radius: 10px;
  margin-bottom: 20px;
  border: 2px solid #fff;
  text-align: center;
  transition: 0.12s linear all;
  wrap: nowrap;
}

footer .button:hover {
  background: #fff;
  color: #000;
}

.footer-info {
  color: #fff;
  max-width: 960px;
  display: flex;
  flex-direction: column;
  text-align: center;
}

.copyright {
  margin-top: 50px;
  color: #ccc;
  font-size: 14px;
}



